.logo
    align-items: center
    display: flex
    height: 30px
    justify-content: center
    width: 50px

header
    align-items: center
    display: flex
    justify-content: space-between
    position: fixed
    width: 100%
    z-index: 2
    //background: #fff !important
    //color: #000 !important
    .logo
        background: rgba(255, 255, 255, 0.5)
        color: #fff

footer
    display: flex
    flex-flow: wrap
    justify-content: center
    .ant-row
        align-items: center
        text-align: center
        //width: 60%
        .logo
            background: rgba(0, 0, 0, 0.2)
            color: #000
    .ant-divider
        border-top-color: #cdcdcd
        min-width: 60%
        width: 60%
    .ant-btn
        .anticon
            svg
                height: 2em
                width: 2em