.container-row
    min-height: 80vh
    padding: 50px 0
    //width: 100%
    &:first-child
        height: 100vh
        position: relative
        .overlay
            background-color: rgba(0, 0, 0, 0.7)
            position: absolute
            top: 0
            bottom: 0
            left: 0
            right: 0
            overflow: hidden
        .ant-typography
            color: white
        .ant-space
            z-index: 1
    &:nth-child(odd)
        background-color: #f7f7f7
    &:nth-child(even)
        background: #fff
    .ant-space-item
        //margin: 0 auto
        //width: 80%
        //text-align: center
        h4.ant-typography
            //margin: 0 auto
            max-width: 600px
    .ant-card
        .ant-card-meta-title
            white-space: unset
    .ant-carousel
        .slick-slide
            display: flex
            justify: center
            align: middle
            .ant-card
                height: 300px
                padding: 40px
                flex-flow: wrap
                h3, h4
                    width: 100%
                h3
                    text-align: center
        .slick-dots 
            li 
                button
                    background: #000 !important
                &.slick-active
                    button
                        background: #000 !important
        
